import { complement, equals, prop } from 'ramda';
import { track } from '../utils/analytics';

const Tags = ({ title, type, tags, selectedTags, setSelectedTags }) => {
  return <div>
    <h2>{title}</h2>
    {tags
      .filter(({ title }) => title !== '-')
      .map(({ code, title }, index) =>
        <button
          key={`${code}_${index}`}
          className={selectedTags.includes(code) ? 'selected' : ''}
          onClick={() => {
            let newSelectedTags = [];
            let isSelected = true;
            if (selectedTags.includes(code)) {
              isSelected = false;
              newSelectedTags = selectedTags.filter(complement(equals)(code));
            } else {
              if (type === 'polar') {
                selectedTags = selectedTags.filter(tagCode => !tags.map(prop('code')).includes(tagCode));
              }
              newSelectedTags = [...selectedTags, code];
            }
            setSelectedTags(newSelectedTags);
            track('Tag Selected', {
              tag: title,
              isSelected,
              selectedTags: newSelectedTags,
            });
          }
        }>
          {title}
        </button>)}
  </div>;
}

export default Tags;
