import { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { track } from "../utils/analytics";

const Activity = ({ tagCodeToTitle, activities }) => {
  const navigate = useNavigate();
  const { id: currentId } = useParams();

  const selectedActivity = activities.find(({ index }) => index === +currentId);

  useEffect(() => {
    if (selectedActivity) {
      track('Activity Viewed', { title: selectedActivity.title });
    }
  })

  if (!selectedActivity) {
    alert('  !');
    return null;
  }

  const {
    title,
    description,
    tags,
    youtubeUrl,
    services,
    teasers,
  } = selectedActivity;

  return <div className="selected-activity">
    <button
      style={{
        cursor: 'pointer',
      }}
      onClick={() => {
        navigate('/activities');
      }}
    >
       
    </button>

    <div>
      <h1>{title}</h1>
      <ul className="tags">
        {(tags || []).map((tag, index) => {return tagCodeToTitle[tag] && <li key={`${tag}_${index}`}>{tagCodeToTitle[tag]}</li>})}
      </ul>
    </div>
    <div className="modal-content">
      {youtubeUrl && (
        <iframe
          width="700"
          height="400"
          style={{ maxWidth: '100%' }}
          src={`https://www.youtube.com/embed/${youtubeUrl}`}
          title="YouTube"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
          onClick={() => {
            track('Activity Video Clicked', {
              activity: title,
            });
          }}
        ></iframe>
      )}
      <p>{description}</p>
      {services && (
        <>
          <h3></h3>
          <ul>
            {services.map(({ title, url }, index) => <li key={`${title}_${index}`}>
              {url
                ? <a
                    href={url}
                    target="_blank"
                    rel="noreferrer"
                    onClick={() => {
                      track('Activity Service Clicked', {
                        activity: selectedActivity.title,
                        title,
                        url,
                      });
                    }}
                  >{title || url}</a>
                : title || url
              }
            </li>)}
          </ul>
        </>
      )}
      <h3></h3>
      <ul>
        {teasers?.map(({ title, url }, index) => <li key={`${title}_${index}`}>
          {url
            ? <a
                href={url}
                target="_blank"
                rel="noreferrer"
                onClick={() => {
                  track('Activity Teaser Clicked', {
                    activity: selectedActivity.title,
                    title,
                    url,
                  });
                }}
              >{title || url}</a>
            : title || url
          }
        </li>)}
        <li><a
          href={`https://www.tiktok.com/search?q=${title.replace(' ', '+')}`}
          target="_blank"
          rel="noreferrer"
          onClick={() => {
            track('Activity Search Clicked', {
              provider: 'TikTok',
              activity: title,
            });
          }}
        >  TikTok</a></li>
        <li><a
          href={`https://www.youtube.com/results?search_query=${title.replace(' ', '+')}`}
          target="_blank"
          rel="noreferrer"
          onClick={() => {
            track('Activity Search Clicked', {
              provider: 'YouTube',
              activity: title,
            });
          }}
        >  YouTube</a></li>
      </ul>

      <h3> </h3>
      <ul>
        <li><a
          href={`https://yandex.ru/maps/?text=${title}`}
          target="_blank"
          rel="noreferrer"
          onClick={() => {
            track('Activity Search Clicked', {
              provider: '.',
              activity: title,
            });
          }}
        >  .</a></li>
      </ul>
      {/* <div className="json">
        {JSON.stringify(selectedActivity)}
      </div> */}
    </div>
  </div>;
}


export default Activity;
